.board {
  width: auto;
  min-width:41vh;

  /*height: 100%;*/
  min-height: 82vh;
  /*max-width: 60vw;*/
  background: var(--TETRIS_PINK);
  border: 3px solid var(--TETRIS_WHITE);
  border-radius: 10px;
  display: grid;
  grid-gap: 2px;
}

.BoardCell {
  width: auto;
  border-radius: 6px;
  position: relative;
}

.tetromino {
  border-top: 3px solid var(--TETROMONO_SHADOW_PRIMARY);
  border-left: 3px solid var(--TETROMONO_SHADOW_PRIMARY);
  border-bottom: 3px solid var(--TETROMONO_SHADOW_SECONDARY);
  border-right: 3px solid var(--TETROMONO_SHADOW_SECONDARY);
}

.tetromino .Sparkle {
  position: absolute;
  z-index: 10;
  width: 5px;
  height: 5px;
  left: -2px;
  top: -2px;
  background-color: var(--TETRIS_WHITE);
  border-radius: 4px 4px;
  opacity: 0.8;
}

.tetromino.ghost {
  background-color: transparent;
  border: 2px solid var(--TETRIS_WHITE);
  opacity: 0.2;
}

.tetromino.ghost .Sparkle {
  opacity: 0;
}

.tetromino__i {
  background-color: var(--TETRIS_RED);
}
.tetromino__j {
  background-color: var(--TETRIS_GRAY);
}
.tetromino__l {
  background-color: var(--TETRIS_WHITE);;
}
.tetromino__o {
  background-color: var(--TETRIS_GREEN);;
}
.tetromino__s {
  background-color: var(--TETRIS_VIOLET);
}
.tetromino__t {
  background-color: var(--TETRIS_BLUE);
}
.tetromino__z {
  background-color: var(--TETRIS_ORANGE);
}
